import {
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import Card from '@mui/material/Card'
import CardActions from '@mui/material/CardActions'
import CardContent from '@mui/material/CardContent'
import IconButton from '@mui/material/IconButton'
import Typography from '@mui/material/Typography'
import { Button, CardHeader } from "@mui/material"
import LaunchIcon from '@mui/icons-material/Launch'
import '@fontsource/roboto';

class CardComponet extends StreamlitComponentBase<any> {

  public render = (): ReactNode => {
    const title = this.props.args["title"]
    const subtitle = this.props.args["subtitle"]
    const body = this.props.args["body"]
    const link = this.props.args["link"]

    return (
      <div style={{ padding: "0.5rem"}}>
        <Card variant="outlined">
          <CardHeader
            title={title}
            subheader={subtitle}
          />
          <CardContent>
            <Typography variant="body2">
              {body}
            </Typography>
          </CardContent>
          <CardActions>
            <IconButton 
             aria-label="open url"
             href={link}
             target="_blank">
              <LaunchIcon />
            </IconButton>
          </CardActions>
        </Card>
      </div>
    )
  }
}

export default withStreamlitConnection(CardComponet)
